import { Component, OnInit, ViewChild } from '@angular/core';
import { BsDatepickerDirective } from 'ngx-bootstrap';

export interface SelectYearDropdown {
  id: number;
  name: string;
}

@Component({
  selector: 'app-system-settings',
  templateUrl: './system-settings.component.html'
})
export class SystemSettingsComponent implements OnInit {
  @ViewChild('dp', { static: false }) datePicker: BsDatepickerDirective;
  public selectYearDropdown: SelectYearDropdown[] = [
    { id: 1, name: '2020' },
    { id: 2, name: '2019' },
    { id: 3, name: '2018' },
  ];
  public selectedYearDropdown = '2020';
  constructor() { }

  myDateValue: Date;
  ngOnInit() {
    this.myDateValue = new Date();
  }

  openAside(): void {
    const body = document.getElementsByTagName('html')[0];
    body.classList.add('aside-open');

    const asidebar = document.getElementsByClassName('asidebar')[0];
    asidebar.scroll(0, 0);
  }

  closeAside(): void {
    const body = document.getElementsByTagName('html')[0];
    body.classList.remove('aside-open');
    const asidebar = document.getElementsByClassName('asidebar');
  }
}
