import { Component, OnInit, TemplateRef } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap';

export interface SelectStatusDropdown {
  id: number;
  name: string;
}
export interface MockPagingDropdown {
  id: number;
  name: string;
}

export interface GroupDropdown {
  id: number;
  name: string;
}

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html'
})
export class UsersComponent implements OnInit {
  public asideIsShow = false;
  public isDsc = false;
  private html;
  public showFeedback;
  public isShowChangePassword = false;
  modalRef: BsModalRef;
  config = {
    backdrop: true,
    ignoreBackdropClick: false
  };

  public selectStatusDropdown: SelectStatusDropdown[] = [
    { id: 1, name: 'Show all' },
    { id: 2, name: 'Active' },
    { id: 3, name: 'Deactivated' },
    { id: 4, name: 'Signed up' }
  ];
  public selectedStatusDropdown = 'Show all';

  public paging: MockPagingDropdown[] = [
    { id: 1, name: '10' },
    { id: 2, name: '20' },
    { id: 3, name: '100' },
  ];
  public selectedPagingDropdown = '20';

  public selectGroupDropdown: GroupDropdown[] = [
    { id: 1, name: 'CSR' },
    { id: 2, name: 'AML' },
    { id: 2, name: 'Admin' },
  ];
  public selectedGroupDropdown = 'Show all';

  constructor(private modalService: BsModalService) { }

  ngOnInit(): void {
    this.html = document.getElementsByTagName('html')[0];

    this.modalService.onHide
      .subscribe(() => {
        this.html.classList.remove('is-show-modal');
      });
  }
  openModal(templateConfirmation: TemplateRef<any>) {
    this.html.classList.add('is-show-modal');
    this.modalRef = this.modalService.show(
      templateConfirmation,
      Object.assign({}, {class: 'anyday-modal dashboard-modal confirmation-modal modal-lg'})
    );
    this.closeAside();
  }
  toShowFeedback(): void {
    this.showFeedback = true;
  }

  openAside(): void {
    const body = document.getElementsByTagName('html')[0];
    body.classList.add('aside-open');

    const asidebar = document.getElementsByClassName('asidebar')[0];
    asidebar.scroll(0, 0);
    this.isShowChangePassword = false;
  }

  closeAside(): void {
    const body = document.getElementsByTagName('html')[0];
    body.classList.remove('aside-open');
    const asidebar = document.getElementsByClassName('asidebar');
  }

  toggleSort(): void {
    this.isDsc = !this.isDsc;
  }

  openChangePassword(): void {
    this.openAside();
    this.isShowChangePassword = true;
  }

  closeChangePassword(): void {
    this.isShowChangePassword = false;
  }
}
