import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { BsModalService, BsModalRef, ModalOptions } from 'ngx-bootstrap/modal';
import { Template } from '@angular/compiler/src/render3/r3_ast';
import { BsDatepickerDirective } from 'ngx-bootstrap';

export interface MockDropdown {
  id: number;
  name: string;
}

export interface SelectOrderDropdown {
  id: number;
  name: string;
}
@Component({
  selector: 'app-debtor-details',
  templateUrl: './debtor-details.component.html'
})

export class DebtorDetailsComponent implements OnInit {
  @ViewChild('dp', { static: false }) datePicker: BsDatepickerDirective;
  public asideIsShow = false;
  public isShowGeneralEdit = false;
  public showFeedback = false;
  public showLoading = false;
  public showManualCapture = false;
  public showManualfee = false;
  private html;
  public mockDropdown: MockDropdown[] = [
    { id: 1, name: 'Item-1' },
    { id: 2, name: 'Transaction date long text' },
    { id: 3, name: 'Show all' },
  ];

  selectedDropdown = 'Show all';

  public selectOrderDropdown: SelectOrderDropdown[] = [
    { id: 1, name: 'Order 1' },
    { id: 2, name: 'Order 2' },
    { id: 3, name: 'Order 3' },
    { id: 4, name: 'Order 4' },
    { id: 5, name: 'Order 5' },
    { id: 6, name: 'Order 6' },
  ];
  // public selectedOrderDropdown = '';
  public isOpenSubRowDetails = true;
  modalRef: BsModalRef;
  config = {
    backdrop: true,
    ignoreBackdropClick: false
  };

  myDateValue: Date;

  constructor(private modalService: BsModalService) { }

  ngOnInit(): void {
    this.myDateValue = new Date();
    this.html = document.getElementsByTagName('html')[0];

    this.modalService.onHide
    .subscribe(() => {
      this.html.classList.remove('is-show-modal');
      });
  }

  openActions(): void {
    const body = document.getElementsByTagName('html')[0];
    body.classList.add('aside-actions-open');
    const asidebar = document.getElementsByClassName('asidebar')[0];
    asidebar.scroll(0, 0);
  }

  openModal(manualCapture: TemplateRef<any>) {
    this.html.classList.add('is-show-modal');
    this.modalRef = this.modalService.show(
      manualCapture,
      Object.assign({}, { class: 'anyday-modal dashboard-modal manual-capture-modal modal-lg' }
      )
    );
  }

  closeActions(): void {
    const body = document.getElementsByTagName('html')[0];
    body.classList.remove('aside-actions-open');
    const asidebar = document.getElementsByClassName('asidebar');
  }

  openAside(): void {
    const body = document.getElementsByTagName('html')[0];
    body.classList.add('aside-open');
    const asidebar = document.getElementsByClassName('asidebar')[0];
    asidebar.scroll(0, 0);
  }

  closeAside(): void {
    const body = document.getElementsByTagName('html')[0];
    body.classList.remove('aside-open');
    const asidebar = document.getElementsByClassName('asidebar');
  }

  toggleSubRowDetails(): void {
    this.isOpenSubRowDetails = !this.isOpenSubRowDetails;
  }

  toShowFeedback(): void {
    this.showFeedback = true;
  }

}
