import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { BsDatepickerDirective } from 'ngx-bootstrap';
import { BsModalService, BsModalRef, ModalOptions } from 'ngx-bootstrap/modal';


export interface SelectTimeDropdown {
  id: number;
  name: string;
}

export interface SelectStatusDropdown {
  id: number;
  name: string;
}


export interface SelectColSorting {
  id: number;
  name: string;
}


export interface SelectOrder {
  id: number;
  name: string;
}
@Component({
  selector: 'app-payouts',
  templateUrl: './payouts.component.html'
})
export class PayoutsComponent implements OnInit {
  @ViewChild('dp', { static: false }) datePicker: BsDatepickerDirective;
  emptyText: string = 'No data';
  unSelectPayout: string = 'Please select payout to view details';
  public asideIsShow = false;
  public isDsc = false;
  public calendarIsShow = false;
  public isShowFilter = true;
  public detailIsShow = false;
  public selectTimeDropdown: SelectTimeDropdown[] = [
    { id: 1, name: 'Last 7 Days' },
    { id: 2, name: 'Today' },
    { id: 3, name: 'Yesterday' },
    { id: 4, name: 'Last Week' },
    { id: 5, name: 'Last Month' },
    { id: 6, name: 'Custom' },
  ];
  public selectedTimeDropdown = 'Last 7 Days';

  public selectStatusDropdown: SelectStatusDropdown[] = [
    { id: 1, name: 'Show all' },
    { id: 2, name: 'Unpaid' },
    { id: 3, name: 'Paid' },
  ];
  public selectedStatusDropdown = 'Show all';

  public selectColSorting: SelectColSorting[] = [
    { id: 1, name: 'Merchant' },
    { id: 2, name: 'Payout Date' },
    { id: 3, name: 'Payout amount' },
    { id: 4, name: 'Status' },
  ];
  public selectedColSorting = 'Merchant';

  public selectOrder: SelectOrder[] = [
    { id: 1, name: 'Ascending' },
    { id: 2, name: 'Descending' }
  ];
  public selectedOrder = 'Descending';
  public isShowDatePicker = false;
  private html;
  public showFeedback;
  modalRef: BsModalRef;
  config = {
    backdrop: true,
    ignoreBackdropClick: false
  };
  checkCustom(): void {
    if (this.selectedTimeDropdown === 'Custom') {
      this.isShowDatePicker = true;
    } else {
      this.isShowDatePicker = false;
    }
  }


  onDateChange(newDate: Date) {
    // console.log(newDate);
  }

  constructor(private modalService: BsModalService) { }

  onShow(event): void {
    this.calendarIsShow = true;
    const dayHoverHandler = event.dayHoverHandler;
    const hoverWrapper = ($event) => {
        const cell = $event;
        cell.isHovered = false;
        return dayHoverHandler($event);
    };
    event.dayHoverHandler = hoverWrapper;

  }

  onHidden(event): void {
    this.calendarIsShow = false;
  }

  myDateValue: Date;
  ngOnInit() {
    this.myDateValue = new Date();

    this.html = document.getElementsByTagName('html')[0];
    this.modalService.onHide
    .subscribe(() => {
      this.html.classList.remove('is-show-modal');
    });
  }

  openAside(): void {
    const body = document.getElementsByTagName('html')[0];
    body.classList.add('aside-open');

    const asidebar = document.getElementsByClassName('asidebar')[0];
    asidebar.scroll(0, 0);
  }

  closeAside(): void {
    const body = document.getElementsByTagName('html')[0];
    body.classList.remove('aside-open');
    const asidebar = document.getElementsByClassName('asidebar');
  }

  openPayoutDetails(): void {
    this.detailIsShow = true;
  }

  closePayoutDetails(): void {
    this.detailIsShow = false;
  }
  openModal(templateConfirmation: TemplateRef<any>) {
    this.html.classList.add('is-show-modal');
    this.modalRef = this.modalService.show(
      templateConfirmation,
      Object.assign({}, {class: 'anyday-modal dashboard-modal confirmation-modal modal-lg'})
    );
    console.log('open Modal');
  }
  toShowFeedback(): void {
    this.showFeedback = true;
  }
}
