import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { BsDatepickerDirective } from 'ngx-bootstrap';
import { SelectTimeDropdown } from '../../merchant/transactions/transactions.component';

export interface SelectColSorting {
  id: number;
  name: string;
}

export interface SelectOrder {
  id: number;
  name: string;
}

@Component({
  selector: 'app-user-details',
  templateUrl: './activity-log.component.html'
})
export class ActivityLogComponent implements OnInit {
  @ViewChild('dp', { static: false }) datePicker: BsDatepickerDirective;
  public asideIsShow = false;
  public isDsc = false;
  public calendarIsShow = false;
  public isShowFilter = true;
  myDateValue: Date;

  public selectTimeDropdown: SelectTimeDropdown[] = [
    { id: 1, name: 'Last 7 Days' },
    { id: 2, name: 'Today' },
    { id: 3, name: 'Yesterday' },
    { id: 4, name: 'Last Week' },
    { id: 5, name: 'Last Month' },
    { id: 6, name: 'Custom' },
  ];
  public selectedTimeDropdown = 'Last 7 Days';

  public isShowDatePicker = false;

  public selectColSorting: SelectColSorting[] = [
    { id: 1, name: 'Time' },
    { id: 2, name: 'Action' },
    { id: 3, name: 'Information' },
  ];
  public selectedColSorting = 'Time';

  public selectOrder: SelectOrder[] = [
    { id: 1, name: 'Ascending' },
    { id: 2, name: 'Descending' }
  ];
  public selectedOrder = 'Descending';

  checkCustom(): void {
    if (this.selectedTimeDropdown === 'Custom') {
      this.isShowDatePicker = true;
    } else {
      this.isShowDatePicker = false;
    }
  }
  ngOnInit() {
    this.myDateValue = new Date();
  }

  onDateChange(newDate: Date) {
    // console.log(newDate);
  }
  constructor(private el: ElementRef) { }


  openAside(): void {
    const body = document.getElementsByTagName('html')[0];
    body.classList.add('aside-filter-open');

    const asidebar = document.getElementsByClassName('asidebar')[0];
    asidebar.scroll(0, 0);
  }

  closeAside(): void {
    const body = document.getElementsByTagName('html')[0];
    body.classList.remove('aside-filter-open');
    const asidebar = document.getElementsByClassName('asidebar');
  }

  toggleSort(): void {
    this.isDsc = !this.isDsc;
  }

  openCalendar(): void {

  }

  onShow(event): void {
    this.calendarIsShow = true;
    const dayHoverHandler = event.dayHoverHandler;
    const hoverWrapper = ($event) => {
        const cell = $event;
        cell.isHovered = false;
        return dayHoverHandler($event);
    };
    event.dayHoverHandler = hoverWrapper;

  }

  onHidden(event): void {
    this.calendarIsShow = false;
  }

}
