import { Component, OnInit, TemplateRef } from '@angular/core';
import { BsModalService, BsModalRef, ModalOptions } from 'ngx-bootstrap/modal';

export interface SelectTypeDropdown {
  id: number;
  name: string;
}
@Component({
  selector: 'app-merchant-details',
  templateUrl: './merchant-details.component.html'
})
export class MerchantDetailsComponent implements OnInit {
  public isDsc = false;
  public asideIsShow = false;
  public isShowGeneralEdit = false;
  public isShowUploadDoc = false;
  public isShowEditDoc = false;
  public popOverCopyMessage = 'Copy to clipboard';
  private html;
  public showFeedback;
  modalRef: BsModalRef;
  config = {
    backdrop: true,
    ignoreBackdropClick: false
  };

  constructor(private modalService: BsModalService) { }

  public selectTypeDropdown: SelectTypeDropdown[] = [
    { id: 1, name: 'Request to change bank document' },
    { id: 2, name: 'General' },
    { id: 3, name: 'Type-1' },
  ];
  public selectedTypeDropdown = 'Request to change bank document';

  ngOnInit(): void {
    this.html = document.getElementsByTagName('html')[0];

    this.modalService.onHide
      .subscribe(() => {
        this.html.classList.remove('is-show-modal');
      });
  }

  toggleSort(): void {
    this.isDsc = !this.isDsc;
  }

  openActions(): void {
    const body = document.getElementsByTagName('html')[0];
    body.classList.add('aside-actions-open');
    const asidebar = document.getElementsByClassName('asidebar')[0];
    asidebar.scroll(0, 0);
  }

  closeActions(): void {
    const body = document.getElementsByTagName('html')[0];
    body.classList.remove('aside-actions-open');
    const asidebar = document.getElementsByClassName('asidebar');
  }

  openAside(): void {
    const body = document.getElementsByTagName('html')[0];
    body.classList.add('aside-open');
    const asidebar = document.getElementsByClassName('asidebar')[0];
    asidebar.scroll(0, 0);
  }

  closeAside(): void {
    const body = document.getElementsByTagName('html')[0];
    body.classList.remove('aside-open');
    const asidebar = document.getElementsByClassName('asidebar');
  }


  openModal(templateConfirmation: TemplateRef<any>) {
    this.html.classList.add('is-show-modal');
    this.modalRef = this.modalService.show(
      templateConfirmation,
      Object.assign({}, {class: 'anyday-modal dashboard-modal confirmation-modal modal-lg'})
    );
  }

  toShowFeedback(): void {
    this.showFeedback = true;
  }

  openGeneralEdit(): void {
    this.openAside();
    this.isShowGeneralEdit = true;
    this.isShowUploadDoc = false;
    this.isShowEditDoc = false;
  }

  openUploadDoc(): void {
    this.openAside();
    this.isShowUploadDoc = true;
    this.isShowGeneralEdit = false;
    this.isShowEditDoc = false;
  }

  openEditDoc(): void {
    this.openAside();
    this.isShowEditDoc = true;
    this.isShowUploadDoc = false;
    this.isShowGeneralEdit = false;
  }

  hidePopOver(): void {
    this.popOverCopyMessage = 'Copy to clipboard';
  }

  changeMessagePopOverCopy(): void {
    this.popOverCopyMessage = 'Copied';
  }

}
