import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TransactionsComponent } from '../admin/transactions/transactions.component';
import { DashboardLayoutComponent } from '../core/dashboard-layout/dashboard-layout.component';
import { DebtorModule } from '../debtor/debtor.module';
import { DebtorsComponent } from './debtors/debtors.component';
import { DebtorDetailsComponent } from './debtor-details/debtor-details.component';
import { MerchantsComponent } from './merchants/merchants.component';
import { MerchantDetailsComponent } from './merchant-details/merchant-details.component';
import { UsersComponent } from './users/users.component';
import { CardPaymentsComponent } from './card-payments/card-payments.component';
import { ActivityLogComponent } from './activity-log/activity-log.component';
import { PayoutsComponent } from './payouts/payouts.component';
import { SystemSettingsComponent } from './system-settings/system-settings.component';
import { PayoutsEmptyComponent } from './payouts-empty/payouts-empty.component';



const routes: Routes = [{
  path: 'dashboard',
  component: DashboardLayoutComponent,
  data: {
    title: 'dashboard'
  },
  children: [
    {
      path: 'transactions',
      component: TransactionsComponent,
      data: {
        title: 'Transaction page'
      }
    },
    {
      path: 'debtors',
      component: DebtorsComponent,
      data: {
        title: 'Debtors page'
      }
    },
    {
      path: 'debtor-details',
      component: DebtorDetailsComponent,
      data: {
        title: 'Debtor details page'
      }
    },
    {
      path: 'merchants',
      component: MerchantsComponent,
      data: {
        title: 'Merchants page'
      }
    },
    {
      path: 'merchant-details',
      component: MerchantDetailsComponent,
      data: {
        title: 'Merchant details page'
      }
    },
    {
      path: 'users',
      component: UsersComponent,
      data: {
        title: 'Users'
      }
    },
    {
      path: 'activity-log',
      component: ActivityLogComponent,
      data: {
        title: 'activity-log'
      }
    },
    {
      path: 'card-payments',
      component: CardPaymentsComponent,
      data: {
        title: 'Card payments'
      }
    },
    {
      path: 'payouts',
      component: PayoutsComponent,
      data: {
        title: 'Payouts'
      }
    },
    {
      path: 'payouts-empty',
      component: PayoutsEmptyComponent,
      data: {
        title: 'Payouts empty'
      }
    },
    {
      path: 'system-settings',
      component: SystemSettingsComponent,
      data: {
        title: 'System settings'
      }
    }
  ]}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdminRoutingModule { }
